.nav a {
  text-decoration: none;
  color: coral;
  text-transform: uppercase; 
  padding: 5px;
  font-size: 20px;
  text-align: center;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
}

.nav span{
  padding-top : 10px;
  padding-bottom: 10px;
}

.nav a:not(:last-child) {
  margin-bottom: 20px;
}